import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';

import jsonAppData from 'utils/jsonAppData';

import { Decor } from 'components/Decor';
import { ButtonV3 } from 'components/Buttons';

const HeroSection: React.FC = () => {
	const sectionTitleEl = useRef<HTMLHeadingElement>(null);
	const buttonGetEl = useRef<HTMLDivElement>(null);
	const buttonRequestEl = useRef<HTMLDivElement>(null);
	const heroImageEl = useRef<HTMLDivElement>(null);
	const decorV3 = useRef<HTMLDivElement>(null);
	const decorV4 = useRef<HTMLDivElement>(null);

	const sectionAnim = () => {
		if (sectionTitleEl.current) {
			const tl = gsap.timeline({
				delay: 0.3,
			});

			tl
				.addLabel('label')
				.fromTo([sectionTitleEl.current, buttonGetEl.current, buttonRequestEl.current], {
					y: 80,
					opacity: 0,
				}, {
					y: 0,
					opacity: 1,
					duration: 0.7,
					ease: 'power1.out',
					stagger: .15,
				})
				.fromTo([heroImageEl.current, decorV3.current], {
					opacity: 0,
					x: gsap.utils.wrap([50, -50]),
				}, {
					x: 0,
					opacity: 1,
					duration: 0.5,
				}, 'label+=.4')
				.from(decorV4.current, {
					opacity: 0,
					scale: 0,
					duration: 0.5,
				}, 'label+=.4');
		}
	};

	useEffect(() => {
		sectionAnim();
	}, []);

	return (
		<section className="section hero_mod" id="home">
			<div className="section_in">
				<Decor
					v3Mod
					decorImg1
					ref={decorV3}
				/>
				<div className="section_row hero_mod">
					<div className="section_col v1_mod">
						<Decor
							v4Mod
							decorImg4
							ref={decorV4}
						/>
						<div className="section_col_in">
							<h1 className="section_title hero_mod offset_v2_mod anim_elem" ref={sectionTitleEl}>{jsonAppData.homePage.heroSection.title}</h1>
							<div className="hero_active_w">
								<div className="hero_btn_w anim_elem" ref={buttonGetEl}>
									<ButtonV3
										url={jsonAppData.homePage.heroSection.getStartedButton.url}
										title={jsonAppData.homePage.heroSection.getStartedButton.title}
									/>
								</div>
								<div className="hero_link_w anim_elem" ref={buttonRequestEl}>
									<a className="link_base" href={jsonAppData.homePage.heroSection.requestButton.url}>{jsonAppData.homePage.heroSection.requestButton.title}</a>
								</div>
							</div>
						</div>
					</div>
					<div className="section_col v2_mod">
						<div className="section_col_in">
							<div className="hero_img_w anim_elem" ref={heroImageEl}>
								<img className="section_img" src={jsonAppData.homePage.heroSection.mainImage} alt={jsonAppData.homePage.heroSection.title} />
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>

	);
};

export default HeroSection;
