import React from 'react';
import classNames from 'classnames';

import { ReactComponent as Decor1 } from 'i/decors/decor_1.svg';
import { ReactComponent as Decor2 } from 'i/decors/decor_2.svg';
import { ReactComponent as Decor3 } from 'i/decors/decor_3.svg';
import { ReactComponent as Decor4 } from 'i/decors/decor_4.svg';
import { ReactComponent as Decor5 } from 'i/decors/decor_5.svg';
import { ReactComponent as Decor6 } from 'i/decors/decor_6.svg';
import { ReactComponent as Decor7 } from 'i/decors/decor_7.svg';
import { ReactComponent as Decor8 } from 'i/decors/decor_8.svg';
import { ReactComponent as Decor9 } from 'i/decors/decor_9.svg';
import { ReactComponent as Decor10 } from 'i/decors/decor_10.svg';
import { ReactComponent as Decor11 } from 'i/decors/decor_11.svg';

interface IProps {
	v1Mod?: boolean,
	v2Mod?: boolean,
	v3Mod?: boolean,
	v4Mod?: boolean,
	v5Mod?: boolean,
	v6Mod?: boolean,
	v7Mod?: boolean,
	v8Mod?: boolean,
	v9Mod?: boolean,
	v10Mod?: boolean,
	v11Mod?: boolean,
	v12Mod?: boolean,
	v13Mod?: boolean,
	v14Mod?: boolean,
	decorImg1?: boolean,
	decorImg2?: boolean,
	decorImg3?: boolean,
	decorImg4?: boolean,
	decorImg5?: boolean,
	decorImg6?: boolean,
	decorImg7?: boolean,
	decorImg8?: boolean,
	decorImg9?: boolean,
	decorImg10?: boolean,
	decorImg11?: boolean,
	animElMod?: boolean,
};

const Decor = React.forwardRef<HTMLDivElement, IProps>(({
	v1Mod,
	v2Mod,
	v3Mod,
	v4Mod,
	v5Mod,
	v6Mod,
	v7Mod,
	v8Mod,
	v9Mod,
	v10Mod,
	v11Mod,
	v12Mod,
	v13Mod,
	v14Mod,
	decorImg1,
	decorImg2,
	decorImg3,
	decorImg4,
	decorImg5,
	decorImg6,
	decorImg7,
	decorImg8,
	decorImg9,
	decorImg10,
	decorImg11,
	animElMod,
}, ref) => {
	const decorClasses = classNames('decor', {
		v1_mod: v1Mod,
		v2_mod: v2Mod,
		v3_mod: v3Mod,
		v4_mod: v4Mod,
		v5_mod: v5Mod,
		v6_mod: v6Mod,
		v7_mod: v7Mod,
		v8_mod: v8Mod,
		v9_mod: v9Mod,
		v10_mod: v10Mod,
		v11_mod: v11Mod,
		v12_mod: v12Mod,
		v13_mod: v13Mod,
		v14_mod: v14Mod,
		anim_elem: animElMod,
	});
	return (
		<div className={decorClasses} ref={ref}>
			{decorImg1 && <Decor1 />}
			{decorImg2 && <Decor2 />}
			{decorImg3 && <Decor3 />}
			{decorImg4 && <Decor4 />}
			{decorImg5 && <Decor5 />}
			{decorImg6 && <Decor6 />}
			{decorImg7 && <Decor7 />}
			{decorImg8 && <Decor8 />}
			{decorImg9 && <Decor9 />}
			{decorImg10 && <Decor10 />}
			{decorImg11 && <Decor11 />}
		</div>
	);
});

export default Decor;
