import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const baseElementsAnimation = (): void => {
	const staggerTitleAnimEl = document.querySelectorAll<HTMLElement>('.staggerTitleAnimEl');

	if (staggerTitleAnimEl.length) {
		staggerTitleAnimEl.forEach(itemEl => {
			setTimeout(() => {
				gsap.fromTo(itemEl, {
					y: 50,
					opacity: 0,
				}, {
					y: 0,
					opacity: 1,
					duration: 0.5,
					stagger: 0.2,
					ease: 'power2.out',
					scrollTrigger: {
						trigger: itemEl,
						start: 'top bottom-=30%',
					},
				});
			}, 250);
		});
	}
};

export default baseElementsAnimation;
