import React from 'react';

import { ReactComponent as EyeIcon } from 'i/icons/eye.svg';
import { ReactComponent as CloseIcon } from 'i/icons/close_2.svg';

interface IProps {
	id?: string,
	type: string,
	name: string,
	label: string,
	placeholder: string,
	isRequired: boolean,
	Icon?: React.ElementType,
	index: number,
}

const FormItem: React.FC<IProps> = ({
	id,
	type,
	name,
	label,
	placeholder,
	index,
	Icon,
	isRequired,
}) => {
	return (
		<div className={`form_field offset_mod`} key="id">
			{type!=='textarea' && 
				<>
					<label className={`form_field_label ${label ? '' : 'hidden_mod'}`} htmlFor={`input_${index}`}>{label}</label>
					<div className="form_field_wrap">
						<input className="form_field_input" id={`input_${index}`} type={type} name={name} placeholder={placeholder}  required={isRequired} />
						{Icon && (
							<div className="form_icon_wrap">
								<Icon className="icon size_mod" />
							</div>
						)}
						{type == 'password' && (
							<a href="#" className="form_icon_wrap">
								<EyeIcon className="icon size_mod" />
							</a>
						)}
						<a href="#" className="form_icon_wrap close_mod hidden_mod">
							<CloseIcon className="icon size_mod" />
						</a>
					</div>
				</>
			}
			{type==='textarea' && 
				<>
					<label className="form_field_label" htmlFor="textarea">{label}</label>
					<textarea className="form_field_textarea" id="textarea" name="textarea" placeholder={placeholder} defaultValue={""} />
				</>
			}
		</div>
	);
};

export default FormItem;
