import React, { useEffect, useRef } from 'react';
import { SectionHead } from 'components/SectionHead';
import jsonAppData from 'utils/jsonAppData';
import { Decor } from 'components/Decor';
import { gsap } from 'gsap';
import { TABLET_MEDIA_POINT } from 'utils/constant';

const HowSection: React.FC = () => {
	const sectionEl = useRef<HTMLDivElement>(null);
	const howImageEl = useRef<HTMLDivElement>(null);
	const howImageContainerEl = useRef<HTMLDivElement>(null);
	const sectionDescrEl = useRef<HTMLDivElement>(null);
	const sectionBaseLinkEl = useRef<HTMLDivElement>(null);
	const decorElV1 = useRef<HTMLDivElement>(null);
	const decorElV2 = useRef<HTMLDivElement>(null);

	const sectionAnim = (): void => {
		if (sectionEl.current) {
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: sectionEl.current,
					start: 'top bottom-=40%',
				},
			});

			const titleEl = sectionEl.current.querySelector<HTMLHeadingElement>('.section_title');
			const subTitleEl = sectionEl.current.querySelector<HTMLDivElement>('.section_subtitle');

			const imageTl = gsap.timeline({
				paused: true,
			});

			imageTl
				.addLabel('entry_start')
				.fromTo(howImageEl.current, {
					opacity: 0,
					scale: 0,
					transformOrigin: '50% 100%',
				}, {
					opacity: 1,
					scale: 1,
					duration: 0.7,
					ease: 'power1.out',
				})
				.fromTo([decorElV1.current, decorElV2.current], {
					opacity: 0,
					scale: 0,
					rotation: gsap.utils.wrap([-90, 90]),
					transformOrigin: '50% 50%',
				}, {
					opacity: 1,
					scale: 1,
					rotation: 0,
					duration: 0.7,
					ease: 'power1.out',
				}, 'entry_start')
				.addLabel('entry_end');

			tl
				.addLabel('label')
				.fromTo([
					subTitleEl,
					titleEl,
					sectionDescrEl.current,
					'.how_item',
					sectionBaseLinkEl.current,
				], {
					y: 50,
					opacity: 0,
				}, {
					y: 0,
					opacity: 1,
					stagger: 0.2,
				}, 'label');

			if (window.innerWidth >= TABLET_MEDIA_POINT) {
				tl
					.add(imageTl.tweenFromTo('entry_start', 'entry_end'), 'label');
			} else {
				if (howImageContainerEl.current) {
					const mobileImageTl = gsap.timeline({
						scrollTrigger: {
							trigger: howImageContainerEl.current,
							start: 'top bottom-=40%',
						},
					});

					mobileImageTl
						.addLabel('start')
						.add(imageTl.tweenFromTo('entry_start', 'entry_end'), 'start');
				}
			}
		}
	};

	useEffect(() => {
		sectionAnim();
	}, []);

	return (
		<section className="section" ref={sectionEl} id="how-it-works">
			<div className="section_in">
				<div className="section_row">
					<div className="section_col v1_mod">
						<div className="section_col_in">
							<SectionHead
								subtitle={jsonAppData.homePage.howSection.subtitle}
								title={jsonAppData.homePage.howSection.title}
								subtitleLeftMod
								titleOffsetMod3
							/>
							<div className="section_descr offset_mod anim_elem" ref={sectionDescrEl}>{jsonAppData.homePage.howSection.text}</div>
							<ul className="how_list">
								{jsonAppData.homePage.howSection.list.map(({
									img,
									title,
									text,
								}, index) => {
									return (
										<li className="how_item anim_elem" key={index}>
											<div className="how_item_img_w">
												<img className="how_item_img" src={img} alt={title} />
											</div>
											<div className="how_item_title">{title}</div>
											<div className="how_item_text">{text}</div>
										</li>
									);
								})}
							</ul>
							<div className="anim_elem" ref={sectionBaseLinkEl}>
								<a className="link_base" href={jsonAppData.homePage.howSection.link.url}>{jsonAppData.homePage.howSection.link.title}</a>
							</div>
						</div>
					</div>
					<div className="section_col v2_mod" ref={howImageContainerEl}>
						<div className="section_col_in">
							<div className="how_img_block">
								<Decor
									v5Mod
									decorImg5
									animElMod
									ref={decorElV1}
								/>
								<Decor
									v6Mod
									decorImg6
									animElMod
									ref={decorElV2}
								/>
								<div className="how_img_w anim_elem" ref={howImageEl}>
									<img className="section_img" src={jsonAppData.homePage.howSection.img} alt="" />
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	);
};

export default HowSection;
