import React from 'react';

interface IProps {
	list: string[],
	active: number
}

const Breadcrumbs: React.FC<IProps> = ({
	list,
	active,
}) => {
	return (
		<div className="breadcrumbs">
			{list.map((item, index) => {
				return (
					<div className={`breadcrumbs_item ${index == active - 1 ? 'active_mod' : ''} ${index < active - 1 ? 'prev_mod' : ''}`}>
						<div className="breadcrumbs_count"></div>
						<div className="breadcrumbs_title">{item}</div>
					</div>
				)
			})}
		</div>
	);
};

export default Breadcrumbs;
