import React, { useEffect, useRef } from 'react';
import Slider from 'react-slick';
import { gsap } from 'gsap';

import { SectionHead } from 'components/SectionHead';
import { SliderControls } from 'components/SliderControls';
import { Decor } from 'components/Decor';

import jsonAppData from 'utils/jsonAppData';

const TestimonialsSection: React.FC = () => {
	const testimonialsListEl = useRef<HTMLDivElement>(null);
	const decorV1El = useRef<HTMLDivElement>(null);
	const decorV2El = useRef<HTMLDivElement>(null);

	const sectionAnim = (): void => {
		if (testimonialsListEl.current) {
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: testimonialsListEl.current,
					start: 'top bottom-=30%',
				},
			});
	
			tl
				.addLabel('start')
				.fromTo('.testimonials_list .slick-slide', {
					xPercent: 10,
					opacity: 0,
				}, {
					xPercent: 0,
					opacity: 1,
					duration: 0.8,
					stagger: 0.15,
					ease: 'power3.out',
				}, 'start')
				.fromTo(['.slider_control.testimonials_mod', decorV1El.current, decorV2El.current], {
					y: 20,
					opacity: 0,
				}, {
					y: 0,
					opacity: 1,
					stagger: 0.3,
				}, 'start+=1');
		}
	};

	const sliderSettings = {
		prevArrow: <SliderControls prevMod testimonialsMod animEl />,
		nextArrow: <SliderControls nextMod testimonialsMod animEl nextArrow />,
		infinite: true,
		slidesToShow: 1,
		slidesToScroll: 1,
		dots: false,
		arrows: true,
		autoplay: false,
		centerMode: true,
		centerPadding: '60px',
		variableWidth: true,
		responsive: [
			{
				breakpoint: 768,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					centerMode: false,
					centerPadding: '0',
					variableWidth: false,
				},
			},
		],
	};

	useEffect(() => {
		sectionAnim();
	}, []);

	return (
		<section className="section">
			<Decor
				v9Mod
				decorImg9
				ref={decorV1El}
				animElMod
			/>
			<Decor
				v10Mod
				decorImg8
				ref={decorV2El}
				animElMod
			/>
			<div className="section_in">
				<SectionHead
					subtitle={jsonAppData.homePage.testimonialsSection.subtitle}
					title={jsonAppData.homePage.testimonialsSection.title}
					titleCenterMod
					titleOffsetMod
					staggerTitleAnimEl
				/>
				<div className="testimonials_wrap" ref={testimonialsListEl}>
					<Slider {...sliderSettings} className="testimonials_list">
						{jsonAppData.homePage.testimonialsSection.testimonialsSlider.map(({
							text,
							img,
							name,
							job,
						}, index) => {
							return (
								<div className="testimonial" key={index}>
									<div className="testimonial_text">{text}</div>
									<div className="testimonial_author">
										<div className="testimonial_avatar">
											<img className="testimonial_avatar_img" src={img} alt={name} />
										</div>
										<div className="testimonial_author_info">
											<div className="testimonial_name">{name}</div>
											<div className="testimonial_job">{job}</div>
										</div>
									</div>
								</div>
							);
						})}
					</Slider>
				</div>
			</div>
		</section>

	);
};

export default TestimonialsSection;
