import React, { useEffect } from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import * as PAGES from './pages';

function App() {
	useEffect(() => {
		document.body.classList.add('loaded_mod');
	}, []);

	return (
		<div className="wrapper">
			<div className="base">
				<BrowserRouter basename="/portfolio-projects/hypercart-react">
					<Route component={PAGES.HomePage} path="/" exact />
					<Route component={PAGES.ContactPage} path="/contact_us" exact />
					<Route component={PAGES.LogIn} path="/login" exact />
				</BrowserRouter>
			</div>
		</div>
	);
}

export default App;
