import React, { useEffect, useRef } from 'react';
import { SectionHead } from 'components/SectionHead';
import { gsap } from 'gsap';
import { Decor } from 'components/Decor';
import { TABLET_MEDIA_POINT } from 'utils/constant';

import jsonAppData from 'utils/jsonAppData';

const PriceSection: React.FC = () => {
	const priceListEl = useRef<HTMLUListElement>(null);

	const sectionAnim = (): void => {
		if (window.innerWidth >= TABLET_MEDIA_POINT && priceListEl.current) {
			const xValue: Array<number> = [-150, 0, 150];
			const yValue: Array<number> = [0, 100, 0];

			gsap.fromTo('.price', {
				x: gsap.utils.wrap(xValue),
				y: gsap.utils.wrap(yValue),
				opacity: 0,
			}, {
				x: 0,
				y: 0,
				opacity: 1,
				duration: 1.1,
				ease: 'power3.out',
				scrollTrigger: {
					trigger: priceListEl.current,
					start: 'top bottom-=40%',
				},
			});
		} else {
			const priceElements = document.querySelectorAll<HTMLLIElement>('.price');

			if (priceElements && priceElements.length) {
				priceElements.forEach(itemEl => {
					gsap.fromTo(itemEl, {
						y: 50,
						opacity: 0,
					}, {
						y: 0,
						opacity: 1,
						duration: 1.1,
						ease: 'power3.out',
						scrollTrigger: {
							trigger: itemEl,
							start: 'top bottom-=40%',
						},
					});
				});
			}
		}
	};

	useEffect(() => {
		sectionAnim();
	}, []);

	return (
		<section className="section bg_mod" id="pricing">
			<Decor
				v8Mod
				decorImg7
			/>
			<div className="section_in">
				<SectionHead
					subtitle={jsonAppData.homePage.priceSection.subtitle}
					title={jsonAppData.homePage.priceSection.title}
					titleColorMod
					titleCenterMod
					titleOffsetMod
					staggerTitleAnimEl
				/>
				<ul className="price_list" ref={priceListEl}>
					{jsonAppData.homePage.priceSection.priceList.map(({
						title,
						img,
						currency,
						price,
						pricePeriod,
						priceAdditional,
						priceAdditionalDescr,
						advantages,
						buttonUrl,
						buttonText,
					}, index) => {
						return (
							<li className="price anim_elem" key={index}>
								<div className={`price_in ${index === 1 ? 'active_mod' : ''}`}>
									<div className="price_img_wrap">
										<img className="price_img" src={img} alt={title} />
									</div>
									<div className="price_title">{title}</div>
									<div className="price_value">
										<div className="price_value_currency">{currency}</div>
										<div className="price_value_data">{price}</div>
										<div className="price_value_period">{pricePeriod}</div>
									</div>
									<div className="price_value_descr">
										{priceAdditional && (
											<>
												<span>{priceAdditional}</span>
												{priceAdditionalDescr}
											</>
										)}
									</div>
									<div className="price_advantages">
										{advantages.map((advantage, index) => {
											return <div className="price_advantages_item" key={index}>{advantage}</div>;
										})}
									</div>
									<div className="btn_wrap end_mod">
										<a className="btn_v2 full_btn" href={buttonUrl}>{buttonText}</a>
									</div>
								</div>
							</li>
						);
					})}
				</ul>
			</div>
		</section>
	);
};

export default PriceSection;
