import React from 'react';
import classNames from 'classnames';

import { ReactComponent as IconArrowPrev } from 'i/icons/left.svg';
import { ReactComponent as IconArrowNext } from 'i/icons/right.svg';

interface IProps {
	onClick?: () => void,
	nextArrow?: boolean,
	prevMod?: boolean,
	nextMod?: boolean,
	testimonialsMod?: boolean,
	animEl?: boolean,
}

const SliderControls: React.FC<IProps> = ({
	nextArrow,
	prevMod,
	nextMod,
	testimonialsMod,
	animEl,
	onClick,
}) => {
	const slideControlClasses = classNames('slider_control', {
		prev_mod: prevMod,
		next_mod: nextMod,
		testimonials_mod: testimonialsMod,
		anim_elem: animEl,
	});

	return (
		<button type="button" className={slideControlClasses} onClick={onClick}>
			{(nextArrow)
				? <IconArrowNext className="icon slider_mod" />
				: (
					<IconArrowPrev className="icon slider_mod" />
				)}
		</button>
	);
};

export default SliderControls;
