import React, { MutableRefObject } from 'react';
import jsonAppData from 'utils/jsonAppData';
import { gsap } from 'gsap';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';
import { TABLET_MEDIA_POINT } from 'utils/constant';

gsap.registerPlugin(ScrollToPlugin);

interface IProps {
	headerElHeight: MutableRefObject<number | undefined>,
	toggleMenuVisibility: () => void
}

const MainMenuList: React.FC<IProps> = ({
	headerElHeight,
	toggleMenuVisibility,
}) => {
	const handleMenuClick = (e: React.MouseEvent<HTMLAnchorElement, MouseEvent>, id: string) => {
		e.preventDefault();

		const targetEl = document.getElementById(id) as HTMLElement;

		if (targetEl && headerElHeight.current) {
			const topY: number = (targetEl.getBoundingClientRect().top + window.scrollY);

			if (window.innerWidth <= TABLET_MEDIA_POINT) {
				toggleMenuVisibility();
			}

			gsap.to(window, {
				scrollTo: {
					y: topY - headerElHeight.current,
					autoKill: false,
				},
				duration: 0.8,
				ease: 'power2.out',
			});
		}
	};

	return (
		<ul className="main_menu_list">
			{jsonAppData.header.menu.map(({
				targetSectionId,
				title,
			}, index) => {
				return (
					<li className={`main_menu_item ${index === 0 ? 'active_mod' : ''}`} key={index}>
						<a
							className="main_menu_link"
							href={targetSectionId}
							onClick={(e) => handleMenuClick(e, targetSectionId)}>
							{title}
						</a>
					</li>
				);
			})}
		</ul>
	);
};

export default MainMenuList;