import { SectionHead } from 'components/SectionHead';
import { gsap } from 'gsap';
import React, { useEffect, useRef, useState } from 'react';
import jsonAppData from 'utils/jsonAppData';
import { chunkArray } from 'utils';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { ISupportedCompany } from 'interfaces';

const SupportedSection: React.FC = () => {
	const supportedBlockEl = useRef<HTMLDivElement>(null);
	const [supportedCompanyList, setSupportedCompanyList] = useState<ISupportedCompany[]>([]);
	const firstSubTimeline = useRef<GSAPTimeline | null>(null);
	const subTimelineDelay: number = 0;

	const globalAnimTl = gsap.timeline({
		paused: true,
		onComplete() {
			gsap.delayedCall(subTimelineDelay, () => {
				if (firstSubTimeline.current) {
					globalAnimTl.seek(firstSubTimeline.current.labels.start_2).play();
				}
			});
		},
	});

	const createPicturesList = (): void => {
		const chunkedGridData = chunkArray(jsonAppData.homePage.supportedSection.picturesList, 9);
		const allSupportedList: ISupportedCompany[] = [];
		let preparedFirstCompanyList: ISupportedCompany[] = [];

		chunkedGridData.forEach(pictureArray => {
			if (pictureArray.length === 9) {
				if (!preparedFirstCompanyList) {
					preparedFirstCompanyList = [...pictureArray];
				}
				pictureArray.forEach((pictureArrayItem: ISupportedCompany) => {
					allSupportedList.push(pictureArrayItem);
				});
			}
		});

		preparedFirstCompanyList.forEach(subItem => {
			allSupportedList.push(subItem);
		});

		setSupportedCompanyList(allSupportedList);
	};

	const createGridAnimation = () => {
		const elements = document.querySelectorAll<HTMLDivElement>('.supported_picture');

		const testArray: Array<HTMLDivElement> = Array.from(elements);
		testArray.reverse();

		let subTimeline: GSAPTimeline = gsap.timeline();

		for (let index = 0; index < (supportedCompanyList.length / 3 - 3); index++) {
			const subTl = gsap.timeline({
				defaults: {
					ease: 'none',
					duration: 2,
				},
			});

			const startYvalue: number = 133.33 * index;
			const yValue: number = 133.33 + 133.33 * index;
			const $stagger = 0;

			if (index === 0) {
				subTl
					.addLabel('start')
					.fromTo([testArray[2], testArray[1], testArray[0]], {
						yPercent: -100,
						opacity: 0,
					}, {
						yPercent: 0,
						immediateRender: false,
						stagger: $stagger,
						opacity: 1,
					}, 'start')
					.fromTo([testArray[5], testArray[4], testArray[3]], {
						yPercent: -100,
						opacity: 0,
					}, {
						yPercent: 0,
						immediateRender: false,
						opacity: 1,
						stagger: $stagger,
					}, 'start')
					.fromTo([testArray[8], testArray[7], testArray[6]], {
						yPercent: -100,
						opacity: 0,
					}, {
						yPercent: 0,
						immediateRender: false,
						opacity: 1,
						stagger: $stagger,
					}, 'start')
					.addLabel('start_2', '+=0')
					.fromTo([testArray[2], testArray[1], testArray[0]], {
						yPercent: startYvalue,
						opacity: 1,
					}, {
						yPercent: yValue,
						opacity: 0,
						immediateRender: false,
						stagger: $stagger,
					}, 'start_2')
					.fromTo([testArray[5], testArray[4], testArray[3]], {
						yPercent: startYvalue,
						opacity: 1,
					}, {
						yPercent: yValue,
						opacity: 1,
						immediateRender: false,
						stagger: $stagger,
					}, 'start_2')
					.fromTo([testArray[8], testArray[7], testArray[6]], {
						yPercent: startYvalue,
						opacity: 1,
					}, {
						yPercent: yValue,
						immediateRender: false,
						stagger: $stagger,
						opacity: 1,
					}, 'start_2')
					.fromTo([testArray[11], testArray[10], testArray[9]], {
						yPercent: startYvalue,
						opacity: 0,
					}, {
						yPercent: yValue,
						opacity: 1,
						immediateRender: false,
						stagger: $stagger,
					}, 'start_2');

				subTimeline = subTl;
			} else {
				subTl
					.addLabel('start')
					.fromTo([testArray[2], testArray[1], testArray[0]], {
						yPercent: startYvalue,
						opacity: 1,
					}, {
						yPercent: yValue,
						opacity: 0,
						immediateRender: false,
						stagger: $stagger,
					}, 'start')
					.fromTo([testArray[5], testArray[4], testArray[3]], {
						yPercent: startYvalue,
						opacity: 1,
					}, {
						yPercent: yValue,
						opacity: 1,
						immediateRender: false,
						stagger: $stagger,
					}, 'start')
					.fromTo([testArray[8], testArray[7], testArray[6]], {
						yPercent: startYvalue,
						opacity: 1,
					}, {
						yPercent: yValue,
						immediateRender: false,
						opacity: 1,
						stagger: $stagger,
					}, 'start');
				if (testArray.length > 9) {
					subTl.fromTo([testArray[11], testArray[10], testArray[9]], {
						yPercent: startYvalue,
						opacity: 0,
					}, {
						yPercent: yValue,
						opacity: 1,
						immediateRender: false,
						stagger: $stagger,
					}, 'start');
				}
			}
			if (testArray.length > 9) {
				testArray.splice(0, 3);
			}

			globalAnimTl.add(subTl, index > 0 ? `+=${subTimelineDelay}` : '');
		}

		firstSubTimeline.current = subTimeline;

		const playTl = (): void => {
			globalAnimTl.play();
		};

		const pauseTl = (): void => {
			globalAnimTl.pause();
		};

		if (supportedBlockEl.current) {
			ScrollTrigger.create({
				trigger: supportedBlockEl.current,
				start: 'top bottom-=10%',
				onEnter: () => {
					playTl();
				},
				onEnterBack: () => {
					playTl();
				},
				onLeaveBack: () => {
					pauseTl();
				},
				onLeave: () => {
					pauseTl();
				},
			});
		}
	};

	useEffect(() => {
		createPicturesList();
	}, []);

	useEffect(() => {
		if (supportedCompanyList) {
			setTimeout(() => {
				createGridAnimation();
			}, 100);
		}
	}, [supportedCompanyList]);

	return (
		<section className="section bg_v2_mod supported_mod" id="vendors">
			<div className="section_in">
				<div className="section_row">
					<div className="section_col v2_mod">
						<div className="section_col_in pointer_none">
							<div className="supported_img_w anim_elem"><img className="section_img" src={jsonAppData.homePage.supportedSection.background} alt="" /></div>
							<div className="supported_pictures_block_w" ref={supportedBlockEl}>
								<div className="supported_pictures_block">
									{supportedCompanyList && (
										supportedCompanyList.map(({
											imgSrc,
											alt,
										}, index) => {
											return (
												<div className="supported_pictures_item" key={index}>
													<div className="supported_picture">
														<div className="supported_picture_img_w">
															<img className="supported_picture_img" src={imgSrc} alt={alt} />
														</div>
													</div>
												</div>
											);
										})
									)}
								</div>
							</div>
						</div>
					</div>
					<div className="section_col v1_mod">
						<div className="section_col_in">
							<SectionHead
								subtitle={jsonAppData.homePage.supportedSection.subtitle}
								title={jsonAppData.homePage.supportedSection.title}
								subtitleLeftMod
								titleOffsetMod3
								staggerTitleAnimEl
							/>
							<div className="section_descr anim_elem staggerTitleAnimEl">{jsonAppData.homePage.supportedSection.text}</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	);
};

export default SupportedSection;
