import React, { useEffect, useRef } from 'react';
import jsonAppData from 'utils/jsonAppData';
import { gsap } from 'gsap';
import MainMenuList from './MainMenuList';

const Header: React.FC = () => {
	const headerEl = useRef<HTMLElement>(null);
	const triggerEl = useRef<HTMLButtonElement>(null);
	const headerElHeight = useRef<number>();

	const toggleMenuVisibility = () => {
		if (triggerEl && triggerEl.current) {
			const bodyEl = document.body;

			if (bodyEl.classList.contains('menu_open')) {
				bodyEl.classList.remove('menu_open');
				triggerEl.current.classList.remove('active_mod');
			} else {
				bodyEl.classList.add('menu_open');
				triggerEl.current.classList.add('active_mod');
			}
		}
	};

	const sectionAnim = () => {
		const tl = gsap.timeline({
			delay: 0.3,
		});

		tl
			.addLabel('label')
			.fromTo(headerEl.current, {
				yPercent: -100,
				opacity: 0,
			}, {
				yPercent: 0,
				opacity: 1,
				duration: 0.7,
				ease: 'power1.out',
				stagger: .15,
			});
	};

	useEffect(() => {
		if (headerEl.current) {
			sectionAnim();
			headerElHeight.current = headerEl.current.offsetHeight;
		}
	}, []);

	return (
		<header className="header" ref={headerEl}>
			<div className="header_in">
				<div className="header_logo_w">
					<img
						className="header_logo"
						src={jsonAppData.header.logo}
						alt="Logo"
					/>
				</div>
				<div className="header_menu_w">
					<button type="button" className="menu_trigger menuTrigger" ref={triggerEl} onClick={toggleMenuVisibility}>
						<span className="menu_trigger_decor" />
					</button>
					<div className="header_menu">
						<MainMenuList headerElHeight={headerElHeight} toggleMenuVisibility={toggleMenuVisibility} />
						<div className="header_active_w">
							<div className="header_log_in">
								<a className="header_log_in_link" href={jsonAppData.header.logIn.url}>
									{jsonAppData.header.logIn.title}
								</a>
							</div>
							<div className="header_btn">
								<a className="btn_base" href={jsonAppData.header.button.url}>
									{jsonAppData.header.button.title}
								</a>
							</div>
						</div>
					</div>
				</div>
			</div>
		</header>
	);
};

export default Header;
