import React, { useEffect } from 'react';

import { Header } from 'components/Header';
import { Footer } from 'components/Footer';
import { Breadcrumbs } from 'components/Breadcrumbs';
import { PopupForm } from 'components/PopupForm';
// import { FormItem } from 'components/FormItem';
import { FormStep } from 'components/FormStep';

import jsonAppData from 'utils/jsonAppData';
// import { ReactComponent as BackIcon } from 'i/icons/back.svg';


const LogIn: React.FC = () => {
	return (
		<>
			<Header />

			<section className="section">
				<div className="section_in">
					<h2 className="section_title center_mod offset_v3_mod">{jsonAppData.LogInPage.title}</h2>
					<div className="section_descr center_mod size_mod offset_2_mod limit_mod">{jsonAppData.LogInPage.text}</div>
					<Breadcrumbs list={jsonAppData.LogInPage.breadcrumbs} active={1} />
					{/* <Breadcrumbs list={jsonAppData.LogInPage.breadcrumbs} active={2} /> */}
					{/* <Breadcrumbs list={jsonAppData.LogInPage.breadcrumbs} active={3} /> */}
					<div className="form_cols">
						<div className="form_wrap">
							<FormStep 
								prevStepLink={jsonAppData.LogInPage.formSteps.stepLogIn.prevStepLink}
								title={jsonAppData.LogInPage.formSteps.stepLogIn.title}
								formInputs={jsonAppData.LogInPage.formSteps.stepLogIn.formInputs}
								mod="v1_mod"
								formCheckboxText={jsonAppData.LogInPage.formSteps.stepLogIn.formCheckboxText}
								buttonSubmitTitle={jsonAppData.LogInPage.formSteps.stepLogIn.buttonSubmitTitle}
								verifyLinkTitle={jsonAppData.LogInPage.formSteps.stepLogIn.verifyLink.title}
								verifyLinkUrl={jsonAppData.LogInPage.formSteps.stepLogIn.verifyLink.url}
							/>

							<FormStep
								prevStepLink={jsonAppData.LogInPage.formSteps.stepPayment.prevStepLink}
								title={jsonAppData.LogInPage.formSteps.stepPayment.title}
								formInputs={jsonAppData.LogInPage.formSteps.stepPayment.formInputs}
								mod="v2_mod"
								buttonSubmitTitle={jsonAppData.LogInPage.formSteps.stepLogIn.buttonSubmitTitle}
							/>
						</div>

						<div className="form_sidebar">
							<div className="form_sidebar_img_wrap">
								<img className="form_sidebar_img" src={jsonAppData.LogInPage.sidebar.img} alt="form" />
							</div>
							<div className="form_sidebar_title">{jsonAppData.LogInPage.sidebar.type}</div>
							<div className="form_sidebar_price">
								<div className="form_sidebar_price_currency">{jsonAppData.LogInPage.sidebar.priceCurrency}</div>
								<div className="form_sidebar_price_amount">{jsonAppData.LogInPage.sidebar.price}</div>
								<div className="form_sidebar_price_period">{jsonAppData.LogInPage.sidebar.pricePeriod}</div>
							</div>
							<div className="form_sidebar_list">
								{jsonAppData.LogInPage.sidebar.list && jsonAppData.LogInPage.sidebar.list.map(item => {
									return (
										<div className="form_sidebar_list_item">{item}</div>
									)
								})}
							</div>
						</div>
					</div>
				</div>
			</section>

			<Footer />

			{/* <PopupForm
				img={jsonAppData.PopupVerify.img}
				title={jsonAppData.PopupVerify.title}
				text={jsonAppData.PopupVerify.text}
				btn={jsonAppData.PopupVerify.btn}
				btnUrl={jsonAppData.PopupVerify.btnUrl}
				bottomText={jsonAppData.PopupVerify.bottomText}
				bottomLink={jsonAppData.PopupVerify.bottomLink}
			/> */}

			{/* <PopupForm
				img={jsonAppData.PopupCongratulations.img}
				title={jsonAppData.PopupCongratulations.title}
				text={jsonAppData.PopupCongratulations.text}
				btn={jsonAppData.PopupCongratulations.btn}
				btnUrl={jsonAppData.PopupCongratulations.btnUrl}
				bottomText={jsonAppData.PopupCongratulations.bottomText}
				bottomLink={jsonAppData.PopupCongratulations.bottomLink}
			/> */}
		</>
	);
};

export default LogIn;
