import React, { useEffect } from 'react';

import { Header } from 'components/Header';
import { Footer } from 'components/Footer';
import {

} from 'components/Sections';
import baseElementsAnimation from 'utils/baseElementsAnimation';
import { Decor } from 'components/Decor';
import { FormItem } from 'components/FormItem';
import jsonAppData from 'utils/jsonAppData';

const ContactPage: React.FC = () => {
	useEffect(() => {
		baseElementsAnimation();
	}, []);

	return (
		<>
			<Header />
			<section className="section">
				<Decor
					v12Mod
					decorImg10
				/>
				<Decor
					v13Mod
					decorImg10
				/>
				<div className="section_in">
					<div className="section_head center_mod offset_mod">
						<div className="section_title offset_v3_mod">{jsonAppData.ContactPage.title}</div>
						<div className="section_descr">{jsonAppData.ContactPage.description}</div>
					</div>
					<div className="section_row indent_mod align_mod">
						<div className="section_col v3_mod">
							<div className="section_col_in indent_mod">
								<Decor
									v14Mod
									decorImg11
								/>
								<div className="contact_us_img_w">
									<div className="contact_us_img"><img className="section_img" src={jsonAppData.ContactPage.image} alt="" /></div>
								</div>
								<ul className="contact_us_list">
									{jsonAppData.ContactPage.contactList.map(({
										Icon, text, url }, index) => {
										return (
											<li className="contact_us_list_item">
												<div className="contact_us_list_icon">
													<Icon className="icon size_mod" />
												</div><a className="contact_us_list_link" href={url}>{text}</a>
											</li>);
									})}
								</ul>
							</div>
						</div>
						<div className="section_col v4_mod">
							<div className="section_col_in">
								<div className="contact_us_block">
									<div className="section_head offset_v2_mod">
										<div className="section_title offset_v4_mod">{jsonAppData.ContactPage.contactBlock.title}</div>
										<div className="section_descr">{jsonAppData.ContactPage.contactBlock.description}</div>
									</div>
									<form className="contact_us_form" action="#" method="POST">
										{jsonAppData.ContactPage.contactBlock.formInputs.map(({
											name, label, placeholder, type, isRequired }, index) => {
											return (
												<FormItem
													type={type}
													name={name}
													label={label}
													placeholder={placeholder}
													isRequired={isRequired}
													index={index}
												/>
											);
										})}
										<button className="btn_v3 full_btn">{jsonAppData.ContactPage.contactBlock.buttonSubmitTitle}</button>
									</form>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
			<Footer />
		</>
	);
};

export default ContactPage;
