import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';

import { Decor } from 'components/Decor';
import jsonAppData from 'utils/jsonAppData';

const Footer: React.FC = () => {
	const footerEl = useRef<HTMLElement>(null);

	const sectionAnim = (): void => {
		if (footerEl.current) {
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: footerEl.current,
					start: 'top bottom-=30%',
				},
			});
	
			tl
				.addLabel('label')
				.fromTo(footerEl.current, {
					y: 50,
					opacity: 0,
				}, {
					y: 0,
					opacity: 1,
					duration: 0.7,
					ease: 'power1.out',
					stagger: .15,
				});
		}
	};

	useEffect(() => {
		sectionAnim();
	}, []);

	return (
		<footer className="footer anim_elem" ref={footerEl}>
			<Decor
				v1Mod
				decorImg7
			/>
			<Decor
				v2Mod
				decorImg8
			/>
			<div className="section_in">
				<div className="footer_top">
					<a className="logo" href="#">
						<img className="logo_img" src={jsonAppData.footer.footerLogo} alt="logo" />
					</a>
					<ul className="social_list footer_mod">
						{jsonAppData.footer.footerSocial.map(({
							url,
							Icon,
						}, index) => {
							return (
								<li className="social_item" key={index}>
									<a className="social_link footer_mod" href={url}>
										<Icon className="icon size_mod" />
									</a>
								</li>
							);
						})}
					</ul>
				</div>
				<div className="footer_bottom">
					{jsonAppData.footer.footerCols.map((item, index) => {
						return (
							<div className="footer_col" key={`${index}_footer_col`}>
								<div className="footer_title">{item.title}</div>
								{item.text && <div className="footer_text">{item.text}</div>}
								{item.link && <a className="footer_link" href={item.url}>{item.link}</a>}
								{item.list && (
									<ul className="footer_list">
										{item.list.map((subitem, index) => {
											return (
												<li className="footer_list_item" key={index}>
													<a className="footer_list_link" href="#">{subitem}</a>
												</li>
											);
										})}
									</ul>
								)}
							</div>
						);
					})}
				</div>
			</div>
		</footer>

	);
};

export default Footer;
