import React from 'react';
import { FormItem } from 'components/FormItem';

import { ReactComponent as BackIcon } from 'i/icons/back.svg';

interface IInputList {
	id?: string,
	type: string,
	name: string,
	label: string,
	placeholder: string,
	isRequired: boolean,
	Icon?: React.ElementType,
}

interface IProps {
	prevStepLink: string,
	title: string,
	formInputs: IInputList[],
	mod: string,
	formCheckboxText?: string,
	buttonSubmitTitle: string,
	verifyLinkTitle?: string,
	verifyLinkUrl?: string,
}

const FormStep: React.FC<IProps> = ({
	prevStepLink,
	title,
	formInputs,
	mod,
	formCheckboxText,
	buttonSubmitTitle,
	verifyLinkTitle,
	verifyLinkUrl,
}) => {
	return (
		<div className={`form_step ${mod}`}>
			{prevStepLink && <a href="#"className="form_back"><BackIcon className="icon back_mod" />{prevStepLink}</a>}
			<div className="form_title">{title}</div>
			<div className="form_list">
				{formInputs && formInputs.map(({
					id,
					type,
					label,
					placeholder,
					isRequired,
					name,
					Icon
				}, index) => {
					return (
						<FormItem 
							id={id}
							type={type}
							label={label}
							name={name}
							placeholder={placeholder}
							isRequired={isRequired}
							Icon={Icon}
							index={index}
						/>
					)
				})}
			</div>
			<div className="form_active_wrap">
				{formCheckboxText && 
					<>
						<div className="form_field  check_mod">
							<label className="form_field_label" htmlFor="check_1">
								<input className="form_field_radiocheck" id="check_1" type="checkbox" name="check_1" />
								<span className="form_field__text form_field_text_check" dangerouslySetInnerHTML={{ __html: formCheckboxText }} />
							</label>
						</div>
					</>
				}
				<div className="btn_wrap offset_mod">
					<button className="btn_v3 full_btn">{buttonSubmitTitle}</button>
				</div>
				{verifyLinkTitle && 
					<>
						<div className="form_verify_link_w">
							<a href={verifyLinkUrl} className="form_verify_link">{verifyLinkTitle}
							<BackIcon className="icon forward_mod" /></a>
						</div>
					</>
				}
			</div>
		</div>
	);
};

export default FormStep;
