import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { SectionHead } from 'components/SectionHead';
import { MOBILE_MEDIA_POINT } from 'utils/constant';

import jsonAppData from 'utils/jsonAppData';

gsap.registerPlugin(ScrollTrigger);

const FeaturesSection: React.FC = () => {
	const featuresListEl = useRef<HTMLUListElement>(null);

	const sectionAnim = (): void => {
		if (window.innerWidth >= MOBILE_MEDIA_POINT && featuresListEl.current) {
			gsap.fromTo('.feature', {
				y: 150,
				opacity: 0,
			}, {
				y: 0,
				opacity: 1,
				duration: 0.7,
				stagger: 0.12,
				ease: 'power2.out',
				scrollTrigger: {
					trigger: featuresListEl.current,
					start: 'top bottom-=30%',
				},
			});
		} else {
			const featureElements = document.querySelectorAll<HTMLLIElement>('.feature');

			if (featureElements && featureElements.length) {
				featureElements.forEach(itemEl => {
					gsap.fromTo(itemEl, {
						y: 50,
						opacity: 0,
					}, {
						y: 0,
						opacity: 1,
						duration: 1.1,
						ease: 'power3.out',
						scrollTrigger: {
							trigger: itemEl,
							start: 'top bottom-=30%',
						},
					});
				});
			}
		}
	};

	useEffect(() => {
		sectionAnim();
	}, []);

	return (
		<section className="section bg_v2_mod">
			<div className="section_in">
				<SectionHead
					subtitle={jsonAppData.homePage.featuresSection.subtitle}
					title={jsonAppData.homePage.featuresSection.title}
					titleOffsetMod
					titleCenterMod
					staggerTitleAnimEl
				/>
				<ul className="features_list" ref={featuresListEl}>
					{jsonAppData.homePage.featuresSection.featuresList.map(({
						img,
						title,
						text,
					}, index) => {
						return (
							<li className="feature anim_elem" key={index}>
								<div className="feature_img_wrap">
									<img className="feature_img" src={img} alt={title} />
								</div>
								<div className="feature_title">{title}</div>
								<div className="feature_text">{text}</div>
							</li>
						);
					})}
				</ul>
			</div>
		</section>
	);
};

export default FeaturesSection;
