import React from 'react';
import classNames from 'classnames';

interface IProps {
	subtitle: string,
	title: string,
	subtitleLeftMod?: boolean,
	titleCenterMod?: boolean,
	titleColorMod?: boolean,
	titleOffsetMod?: boolean,
	titleOffsetMod3?: boolean,
	staggerTitleAnimEl?: boolean,
}

const SectionHead : React.FC<IProps> = ({
	subtitle,
	title,
	subtitleLeftMod,
	titleCenterMod,
	titleColorMod,
	titleOffsetMod,
	titleOffsetMod3,
	staggerTitleAnimEl,
}) => {
	const subtitleClasses = classNames('section_subtitle anim_elem', {
		left_mod: subtitleLeftMod,
		staggerTitleAnimEl,
	});

	const titleClasses = classNames('section_title anim_elem', {
		color_mod: titleColorMod,
		center_mod: titleCenterMod,
		offset_mod: titleOffsetMod,
		offset_v3_mod: titleOffsetMod3,
		staggerTitleAnimEl,
	});

	return (
		<>
			<div className={subtitleClasses}>{subtitle}</div>
			<h2 className={titleClasses} dangerouslySetInnerHTML={{ __html: title }} />
		</>
	);
};

export default SectionHead;
