import React, { useEffect } from 'react';

import { Header } from 'components/Header';
import { Footer } from 'components/Footer';
import {
	BannerSection,
	HeroSection,
	HowSection,
	SupportedSection,
	FeaturesSection,
	PriceSection,
	TestimonialsSection,
} from 'components/Sections';
import baseElementsAnimation from 'utils/baseElementsAnimation';

const HomePage: React.FC = () => {
	useEffect(() => {
		baseElementsAnimation();
	}, []);

	return (
		<>
			<Header />
			<HeroSection />
			<SupportedSection />
			<HowSection />
			<FeaturesSection />
			<PriceSection />
			<TestimonialsSection />
			<BannerSection />
			<Footer />
		</>
	);
};

export default HomePage;
