import React, { useEffect, useRef } from 'react';
import { Decor } from 'components/Decor';
import { gsap } from 'gsap';

import jsonAppData from 'utils/jsonAppData';

const BannerSection: React.FC = () => {
	const bannerSectionEl = useRef<HTMLElement>(null);
	const bannerTitleEl = useRef<HTMLHeadingElement>(null);
	const bannerTextEl = useRef<HTMLDivElement>(null);
	const bannerButtonEl = useRef<HTMLDivElement>(null);

	const sectionAnim = (): void => {
		if (bannerSectionEl.current) {
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: bannerSectionEl.current,
					start: 'top bottom-=30%',
				},
			});
	
			tl
				.addLabel('label')
				.fromTo(bannerSectionEl.current, {
					opacity: 0,
				}, {
					opacity: 1,
					duration: 0.7,
					ease: 'power1.out',
				})
				.addLabel('stagger', '-=.4')
				.fromTo([bannerTitleEl.current, bannerTextEl.current], {
					y: 50,
					opacity: 0,
				}, {
					y: 0,
					opacity: 1,
					stagger: 0.2,
				}, 'stagger')
				.fromTo(bannerButtonEl.current, {
					x: 50,
					opacity: 0,
				}, {
					x: 0,
					opacity: 1,
				}, 'stagger');
		}
	};

	useEffect(() => {
		if (bannerSectionEl && bannerSectionEl.current) {
			sectionAnim();
		}
	}, []);

	return (
		<section className="section anim_elem" ref={bannerSectionEl}>
			<div className="section_in">
				<div className="banner">
					<Decor
						v7Mod
						decorImg3
					/>
					<Decor
						v11Mod
					/>
					<div className="banner_content">
						<h2 className="banner_title anim_elem" ref={bannerTitleEl}>{jsonAppData.homePage.bannerSection.title}</h2>
						<div className="banner_text anim_elem" ref={bannerTextEl}>{jsonAppData.homePage.bannerSection.text}</div>
					</div>
					<div className="btn_wrap offset_side_mod anim_elem" ref={bannerButtonEl}>
						<a className="btn_v3" href={jsonAppData.homePage.bannerSection.buttonUrl}>{jsonAppData.homePage.bannerSection.buttonText}</a>
					</div>
				</div>
			</div>
		</section>
	);
};

export default BannerSection;
